"use client";

import { useCallback, useEffect, useState } from "react";
import {
  Table,
  TableHeader,
  TableColumn,
  TableBody,
  TableRow,
  TableCell,
  type SortDescriptor,
} from "@heroui/table";
import {
  Dropdown,
  DropdownItem,
  DropdownMenu,
  DropdownTrigger,
} from "@heroui/dropdown";
import { Button } from "@heroui/button";
import { Chip } from "@heroui/chip";
import { User } from "@heroui/user";
import { TopToolbar } from "./toolbar/top";
import { BottomToolbar } from "./toolbar/bottom";
import { PiDotsThreeOutlineVerticalFill } from "react-icons/pi";
import { getError, getRequest } from "@/utils/axios";
import { ENDPOINTS } from "@/shared/site";
import { AxiosErrorProps } from "@/types";
import { useAlertStore } from "@/providers/alert";
import useDate from "@/hooks/date";
import { SharedSelection } from "@heroui/system";

interface ListResultProps {
  id: number;
  first_name: string;
  last_name: string;
  email: string;
  picture: {
    portrait: null | string;
  };
  role: "user" | "admin" | "superadmin";
  created_at: string;
  last_password_update: null | string;
  regular_auth: boolean;
  is_active: boolean;
  is_blocked: boolean;
}

interface GetListParams {
  page?: number;
  page_size?: string;
  order?: SortDescriptor;
  q?: string;
}

interface TableListProps {
  page: number;
  pages: number;
  total: number;
  page_size: string;
  q: string;
  order: SortDescriptor;
  results: ListResultProps[];
}

export function UserTable() {
  const [list, setList] = useState<TableListProps>({
    page: 1,
    pages: 0,
    total: 0,
    page_size: "5",
    q: "",
    order: {
      column: "created_at",
      direction: "descending",
    },
    results: [],
  });
  const [selectedItems, setSelectedItems] = useState<SharedSelection>(
    new Set([])
  );
  const [isLoading, setIsLoading] = useState(true);
  const { setAlert } = useAlertStore();
  const { maskDate } = useDate();

  const getList = useCallback(
    async ({
      page = list.page,
      page_size = list.page_size,
      order = list.order,
      q = list.q,
    }: GetListParams = {}) => {
      try {
        const { data } = await getRequest({
          url: `${
            ENDPOINTS.user_list
          }?page=${page}&page_size=${page_size}&ordering=${
            order.direction === "ascending" ? order.column : `-${order.column}`
          }${q ? `&q=${q}` : ""}`,
        });

        setList((prev) => ({
          ...prev,
          page,
          page_size,
          q,
          total: data.count,
          order,
          pages: Math.ceil(data.count / Number(page_size)),
          results: data.results,
        }));
      } catch (err) {
        const { data, status } = getError(err as AxiosErrorProps);
        if (status && status >= 500) {
          setAlert((prev) => ({
            ...prev,
            isVisible: true,
            color: "danger",
            description: "Internal server error.",
          }));
        } else {
          setAlert((prev) => ({
            ...prev,
            isVisible: true,
            color: "danger",
            description: data?.message,
          }));
        }
      } finally {
        setIsLoading(false);
      }
    },
    [list]
  );

  const handleSortChange = useCallback(
    (descriptor: SortDescriptor) => {
      getList({ order: descriptor });
    },
    [list]
  );

  useEffect(() => {
    getList();
  }, []);

  return (
    <Table
      isCompact
      removeWrapper
      aria-label="Account table"
      bottomContentPlacement="outside"
      checkboxesProps={{
        size: "sm",
        classNames: {
          wrapper: "after:bg-foreground after:text-background text-background",
        },
      }}
      classNames={{
        wrapper: ["max-h-[382px]", "max-w-3xl"],
        th: [
          "bg-transparent",
          "text-default-500",
          "border-b",
          "border-divider",
        ],
        td: [
          "first:group-data-[first=true]/tr:before:rounded-none",
          "last:group-data-[first=true]/tr:before:rounded-none",
          "group-data-[middle=true]/tr:before:rounded-none",
          "first:group-data-[last=true]/tr:before:rounded-none",
          "last:group-data-[last=true]/tr:before:rounded-none",
        ],
      }}
      selectedKeys={selectedItems}
      // selectionMode="multiple"
      sortDescriptor={list.order}
      // topContent={
      //   <TopToolbar
      //     selectedItems={selectedItems}
      //     list={list}
      //     getList={getList}
      //   />
      // }
      // bottomContent={
      //   list.pages > 1 ? (
      //     <BottomToolbar
      //       selectedItems={selectedItems}
      //       list={list}
      //       getList={getList}
      //     />
      //   ) : null
      // }
      // topContentPlacement="outside"
      onSelectionChange={setSelectedItems}
      onSortChange={handleSortChange}
    >
      <TableHeader>
        <TableColumn key="first_name" align="start" allowsSorting={true}>
          IDENTITY
        </TableColumn>
        <TableColumn key="role" align="start" allowsSorting={true}>
          ROLE
        </TableColumn>
        <TableColumn key="is_active" align="center" allowsSorting={true}>
          STATUS
        </TableColumn>
        <TableColumn key="is_blocked" align="center" allowsSorting={true}>
          RESTRICTION
        </TableColumn>
        <TableColumn key="created_at" align="start" allowsSorting={true}>
          CREATED AT
        </TableColumn>
        <TableColumn key="actions" align="end">
          ACTIONS
        </TableColumn>
      </TableHeader>
      <TableBody
        emptyContent={"No users found"}
        items={list.results}
        // loadingContent={<Spinner variant="dots" />}
        isLoading={isLoading}
      >
        {(item) => {
          return (
            <TableRow key={item.id}>
              <TableCell>
                <User
                  avatarProps={{
                    radius: "full",
                    size: "sm",
                    src: item.picture.portrait ?? "",
                  }}
                  classNames={{
                    description: "text-default-500",
                  }}
                  description={item.email}
                  name={`${item.first_name} ${item.last_name}`}
                >
                  {item.email}
                </User>
              </TableCell>
              <TableCell>
                <span className="uppercase">{item.role}</span>
              </TableCell>
              <TableCell>
                <Chip
                  className="uppercase border-none gap-1 text-default-600"
                  color={item.is_active ? "success" : "danger"}
                  variant="dot"
                >
                  {item.is_active ? "Active" : "Inactive"}
                </Chip>
              </TableCell>
              <TableCell>
                <Chip
                  className="uppercase border-none gap-1 text-default-600"
                  color={item.is_blocked ? "success" : "danger"}
                  variant="dot"
                >
                  {item.is_blocked ? "Blocked" : "None"}
                </Chip>
              </TableCell>
              <TableCell>{maskDate(item.created_at, "dd/mm/yyyy")}</TableCell>

              <TableCell>
                <Dropdown className="bg-background border-1 border-default-200">
                  <DropdownTrigger>
                    <Button isIconOnly radius="full" size="sm" variant="light">
                      <PiDotsThreeOutlineVerticalFill className="text-default-500 w-4 h-4" />
                    </Button>
                  </DropdownTrigger>
                  <DropdownMenu>
                    <DropdownItem key="view">View</DropdownItem>
                    <DropdownItem key="edit">Edit</DropdownItem>
                    <DropdownItem key="delete">Delete</DropdownItem>
                  </DropdownMenu>
                </Dropdown>
              </TableCell>
            </TableRow>
          );
        }}
      </TableBody>
    </Table>
  );
}
